import {ajax,setTime,showTips} from '@/ajax.js'
export default {
	data() {
		return {
			id:'',
			info:'',  		//初始化数据
			toUserName:'',  //留言的对象
			toUserId:'',	//留言的对象ID
			comment:''      //回复帖子内容
		}
	},
	onLoad(e) {
		this.id = e.id
		this.infoFun()
	},
	methods: {
		infoFun(){
			let _this = this;
			let login = uni.getStorageSync('login')
			if(login!=''&&login!=null&&login!=undefined){
				uni.request({
					url: ajax.detail+ this.id, //仅为示例，并非真实接口地址。
					header:{
						Authorization:'bearer' +' '+ login
					},
					success: (res) => {
						if(res.data.code==200){
							_this.info = res.data.data;
							_this.info.newTime = setTime(res.data.data.date)
							_this.info.comments.map(es=>{
								es.newTime = setTime(es.date)
							})
						}
					}
				});
			}
		},
		toUser(e){
			console.log(e)
			this.toUserName = e.author
			this.toUserId = e._id
		},
		submit(){
			let login = uni.getStorageSync('login')
			if(login!=''&&login!=null&&login!=undefined){
				if(this.toUserName!=''){ //给别人留言
					uni.request({
						url: ajax.replyreply,
						header:{
							Authorization:'bearer' +' '+ login
						},
						method:'POST',
						data:{
							blog_id: this.info._id,
							reply_id: this.toUserId,
							comment: this.comment
						},
						success: (res) => {
							if(res.data.code==200){
								showTips('留言成功')
								this.toUserName='';
								this.comment='';
								this.toUserId='';
								this.infoFun()
							}
						}
					});
				}else{  //给文章留言
					uni.request({
						url: ajax.reply,
						header:{
							Authorization:'bearer' +' '+ login
						},
						method:'POST',
						data:{
							id: this.info._id,
							comment: this.comment
						},
						success: (res) => {
							if(res.data.code==200){
								showTips('留言成功')
								this.toUserName='';
								this.comment='';
								this.toUserId='';
								this.infoFun()
							}
						}
					});
				}
			}
		}
	}
}
